.TokenTypeHolder__content-block {
  height: 443px;
  padding: 20px;
  margin: 20px;
  border-radius: 5px;
  background-color: #252525;
  border: solid 1px #313131;
  display: grid;
}
@media (min-width: 768px) {
  .TokenTypeHolder__content-block {
    width: 255px;
  }
}
@media (min-width: 1536px) {
  .TokenTypeHolder__content-block {
    width: 320px;
  }
}
.TokenTypeHolder__content-block-title {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0;
  text-align: center;
  color: white;
}
.TokenTypeHolder__content-block-img {
  width: 141px;
  height: 115px;
}
.TokenTypeHolder__content-block-desc {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0;
  text-align: center;
  color: white;
  font-size: 16px;
  color: #828ea2;
  height: 105px;
  margin-top: 30px;
}



