.PageContent__hide {
  visibility: hidden;
}
.PageContent__visible {
  visibility: visible;
}
.PageContent-content {
  border-radius: 5px;
  background-color: #252525;
  border: solid 1px #313131;
}
.PageContent-content_wrapContent {
  border-radius: 5px;
  background-color: #252525;
  border: solid 1px #313131;
  display: inline-block;
}



