.TokenWeightDialog__content {
  text-align: center;
}
.TokenWeightDialog__content .ant-slider-track,
.TokenWeightDialog__content .ant-slider-track:hover,
.TokenWeightDialog__content .ant-slider-track:focus {
  background-color: transparent !important;
}
.TokenWeightDialog__content-text {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0;
  color: #828ea2;
  margin: 10px 0;
  text-align: left;
}
.TokenWeightDialog__content-text-date {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0;
  color: #828ea2;
  margin: 10px 0;
  text-align: left;
  float: left;
  margin: 20px 0;
}
.TokenWeightDialog__content-text-date-value {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0;
  color: #828ea2;
  margin: 10px 0;
  text-align: left;
  float: left;
  margin: 20px 0;
  float: right;
  color: #333b42;
}
.TokenWeightDialog__content-date-slider {
  clear: both;
}



