.TokenWeightHolder__content {
  border-radius: 8px;
  background-color: #252525;
  border: solid 1.2px #151515;
  width: 160px;
  height: 70px;
  padding: 3px 4px 0 7px;
  margin-right: 20px;
}
.TokenWeightHolder__content-data {
  float: left;
  width: 130px;
}
.TokenWeightHolder__content-actions {
  float: right;
  margin-top: -3px;
}
.TokenWeightHolder__item-date {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0;
  color: #828ea2;
}
.TokenWeightHolder__item-key {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0;
  color: #828ea2;
  font-weight: lighter;
  color: white;
}
.TokenWeightHolder__item-value {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0;
  color: #828ea2;
  font-weight: lighter;
  color: white;
  float: right;
}
.TokenWeightSimpleHolder__content {
  border-radius: 8px;
  background-color: #252525;
  border: solid 1.2px #151515;
  width: 160px;
  padding: 3px 4px 0 7px;
  margin-right: 20px;
  padding: 0;
  background: transparent;
  border: none;
  width: auto;
  height: 70px;
}
.TokenWeightSimpleHolder__content-data {
  float: left;
  width: 130px;
  width: auto;
}
.TokenWeightSimpleHolder__item-date {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0;
  color: #828ea2;
  color: white;
}
.TokenWeightSimpleHolder__item-key {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0;
  font-weight: lighter;
  color: white;
  width: 160px;
  display: inline-block;
  color: #828ea2;
}
.TokenWeightSimpleHolder__item-value {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0;
  color: #828ea2;
  font-weight: lighter;
  color: white;
}



