.PageHeader {
  color: white;
  height: 60px;
  line-height: 55px;
  background-color: #252525;
  padding: 0 20px;
  display: flow-root;
  white-space: nowrap;
}
.PageHeader__logo {
  margin-left: 30px;
}
.PageHeader__logo-img {
  width: 42px;
  height: 40px;
  margin-right: 10px;
}
.PageHeader__logo-text,
.PageHeader__logo-text:hover,
.PageHeader__logo-text:focus,
.PageHeader__logo-text:active {
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0;
  color: white;
  text-decoration: none;
}
.PageHeader__btn-block {
  float: right;
}
.PageHeader__btn-block a:first-child {
  margin-right: 30px;
}
.PageHeader__btn-block a:last-child {
  margin-right: 10px;
}
.PageHeader__btn {
  width: 30px;
  height: 30px;
}



